h1, h2, h3, h4, ul, li, button{
    color: var(--font-color);
    text-transform: uppercase;
    letter-spacing: 1px;
    white-space: nowrap;
    text-decoration: none;
    font-style: normal;
    border: none;
    background-color: transparent;
    margin: 0;
    transition: 0.3s ease-in-out;
}

p{
    color: var(--font-color);
    letter-spacing: 1px;
    margin: 0;
    transition: 0.3s ease-in-out;
}

h1{
    font-size: 2em;
    margin-bottom:15px;
}

h3{
    margin-top: 20px;
}

h4{
    font-size: 1em;
    margin-bottom: 10px;
    border-bottom: 1px solid var(--font-color);
    color: #a6adb4;
}

h6{
    margin: 0px;
}

ul{
    padding-left: 0px;
}

.intro-h4{
    border-bottom: 0px;
}

button{
    color: var(--font-color);
    background: transparent;
    border: none;
    font-weight: 600;
}

.button{
    margin: 10px;
    justify-self: left;
    margin-left: 0px;
    padding-left: 0px;
}
.buttonC, .button{
    text-transform: uppercase;
    letter-spacing: 1px;
    white-space: nowrap;
    color: var(--font-color);
    font-weight: 100;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    &:hover {
        transform: translateY(-3px);
        color: #CF3F3A;
    }
}

.boxW, .boxB, .boxR {
    display: inline-block;
    width: 10px; 
    height: 10px; 
    margin-right: 10px; 
    cursor: pointer;
    border: 1px solid var(--font-color);
}

.boxW{background-color: #F4F6F6; }
.boxB {background-color: #1A1E23; }



.page{
    position: relative;
    height: 100vh; 
    width: 100%;
}

.colourPanel{
    position: fixed;
    display:flex;
    bottom: 0;
    margin-bottom: 15px;
    backdrop-filter: blur(3px);
    ul{
        padding-right: 40px;
    }
}
.order{
    position: relative;
    display: flex;
    height: calc(100% - (3rem + 65px));
}

.links{
    margin-top: 30px;
    position: sticky;
    display:grid;
    justify-content: start;
    width: 120px;
    border-right: 1px solid var(--font-color);
    height: 185px;
    top: 50px;
}

.page-display{
    display: flex;
    flex-direction: column;
    padding: 20px;
    margin-left: 20px;
    width: calc(100% - 120px);
}

.display{
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    border: 3px solid black;
    height: 500px;
    width: 45vw;
    overflow: visible;
}

.artex-box, .store{
    list-style: none;
    margin: 0px;
    li{margin: 0;}
}
  
.projectUL{
    position: relative;
    display: flex;
    height: 650px;
    justify-content: center;
}

#react-unity-webgl-canvas-2{
    width: 900px;
    aspect-ratio: 2 / 1;
}

.titles {
    text-align: center;
    padding: 0; /* Add this line to override the default margin */
}

.site-video{
    width: 700px;
}

.infectionSim{
    height: 800px;
    width: 800px;
}

@media (max-width: 1620px) {
    .artex-box, .store{
        margin: 10px;
    }
}

@media (max-width: 1340px) {
    .artex-box, .store{
        margin: 0;
    }
}

@media (max-width: 1295px) {
    .projectUL{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: auto;
    }

    .display, .site-video{
        width: 80vw;
        aspect-ratio: 16 / 9;
        max-width: 700px;
        
    }

    #react-unity-webgl-canvas-2{
        width: 80vw;
        aspect-ratio: 2 / 1;
        max-width: 700px;
    }

}


@media (max-width: 600px){
    h1, h2, h3, h4, ul, li, button{
        white-space:inherit;
    }    
}